import DdzBaseHandler from "./DdzBaseHandler";

export default class DdzDealHandler extends DdzBaseHandler{
    protected execute(msg){
        let userCards = msg._para.UserCards || [];
        this.setSelfPokers(userCards);
        this.mainUI.showActionTimer(false);
        this.mainUI.dealPokersAnim();
    }

    protected executeSync(msg){
        let userCards = msg._para.UserCards || [];
        this.setSelfPokers(userCards);
        this.mainUI.dealPokerDirect(userCards);
    }

    private setSelfPokers(usercards){
        usercards.forEach((user) => {
            let localSeat = this.SitHelper.serverSToLocalN(user.chair);
            if (localSeat == 0){
                if (user.all_cards && !Global.Toolkit.isEmptyObject(user.all_cards)){
                    this.mainUI.selfPlayView.setGroupPokersValue(user.all_cards);
                    this.context.addSelfHandPokers(user.all_cards, true);
                }
            }
        });
    }
}